.home {
  height: 100vh;
  background-color: #dadfe4;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  margin:0;
}



@media only screen and (max-width:768px) {

  .home {
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top right;
  }

}
